
body {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  background-color: #f2f2f2;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.container {
  background-color: rgb(102, 193, 238);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

fieldset {
  border: 2px solid rgb(7, 255, 172);
  border-radius: 5px;
  padding: 10px;
  height: auto;
}

legend {
  background-color: aquamarine;
  color: brown;
  font-size: large;
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid brown;
}

tr {
  padding: 10px;
}

label {
  display: inline-block;
  margin-bottom: 5px;
  color: black;
}

/* input, */
input {
  width: 100%;
  border-radius: 3px;
}
input.gen1 {
  width: auto;
  box-sizing: border-box;
}
input.gen2 {
  width: auto;
  box-sizing: border-box;
}
input,
select {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}

button {
  background-color: #4caf50;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  /* align-self: center; */
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: auto;
  margin-right: auto;
}

button:hover {
  background-color: #45a049;
}
table {
  width: 100%;
  border-collapse: collapse;
  background-color: rgb(48, 157, 239);
  height: 575px;
}

td {
  border: 2px solid black;
  padding: 10px;
  text-align: Left;
  width: 384.83px;
}

.center {
  width: 50%;
  margin: auto;
  padding: 20px;
  align-items: center;
}
table.ButtonTable {
  margin: auto;
  height: auto;
}
td.ButtonData {
  border: 0px;
  text-align: center;
}
.register-button:hover {
  background-color: white;
}
.custom-register-button:hover {
  background-color: goldenrod;
}
.back-link {
  display: block;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
  background-color: goldenrod;
  text-decoration: none;
  transition: color 0.3s ease;
}
.back-link:hover {
  background-color: rgb(236, 36, 166);
}
.custom-register-button {
  display: block;
  width: 50%;
  padding: 12px;
  /* text-align: center; */
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left: auto;
  margin-right: auto;
  background-color: #0fff1b;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}