.container {
  max-width: 400px;
  margin: 100px auto;
  padding: 20px;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.login-form {
  display: flex;
  flex-direction: column;
}

.label {
  font-weight: bold;
  color: #555;
  margin-bottom: 5px;
}

.input-container {
  position: relative;
  margin-bottom: 15px;
}

input[type="text"],
input[type="password"] {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f2f2f2;
  width: calc(100% - 22px); /* Adjusted for border width */
}

.input-container::after {
  content: "";
  position: absolute;
  bottom: -1px; /* Adjusted to overlap border */
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #007bff;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.input-container:hover::after {
  transform: scaleX(1);
}

.custom-register-button {
  padding: 12px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.custom-register-button:hover {
  background-color: #0056b3;
}

.link-btn {
  color: #007bff;
  text-decoration: none;
  transition: color 0.3s ease;
}

.link-btn:hover {
  color: #0056b3;
}

.register-text {
  text-align: center;
  margin-top: 20px;
}
